@import '~xtend-library/src/core/row/_row.less';

/**
 * vars
 */

// generate class: [false], [true] non responsive only, [sm, md, lg] responsive @breakpoints

@generate-row: true;

// row

@row-spaces: none, micro, tiny, small, medium, big, giant, huge; // if you change this change also variables names below
@row-space-none: 0 0; // vertical horizontal
@row-space-micro: .25rem .25rem;
@row-space-tiny: .5rem .5rem;
@row-space-small: .75rem .75rem;
@row-space-medium: 1rem 1rem;
@row-space-big: 1.5rem 1.5rem;
@row-space-giant: 2rem 2rem;
@row-space-huge: 3rem 3rem;

/**
 * row
 */

// variant

.row-default {
  .row-space(medium);
  .row-space(big, sm);
}
